.cart-item {
      height: 200px;
      width: 333px;
      margin: 40px auto ;
      padding: 24px 10px;
      display: flex;
      flex-direction:row;
      border: 50px solid gainsboro;
      border-radius: 43px;
      justify-content: space-around;
      
}
.left-block{
    padding-top: 40px;
}
 .item-image-style{

    height: 109px;
    width: 131px;
    border-Radius: 10%;
    background-color:'#ccc';
    

}
.items-fonts{
       height: 50px;
       width: auto;
       color: gray;
      
       
       
    }



.right-block {
    
    display: flex;
    flex-direction: column;
    justify-content: space-between;

}
.upper-block{
    
    display: flex;
    flex-direction: column;

}



.action-icons {
    height: 31px;
    width: 31px;
    margin: 14px;
}

.itemName{
    font-size: 30px;
        margin-left: .5em;

}